import { useState, useMemo, useEffect } from 'react';
import dayjs from 'dayjs';
import { Popup } from 'antd-mobile';
import { Target, TrendingUp, Tent, Trophy, BarChart3, ClipboardList, CheckCircle, Calendar } from 'lucide-react';
import type { MainlineTaskType, NumericDirection, CheckInUnit } from './types';
import { CycleCalculator } from './utils/cycleCalculator';
import { useTheme } from './settings/theme';

interface CreateMainlineTaskModalProps {
  visible: boolean;
  onClose: () => void;
  onSubmit: (taskData: any) => void;
}

// 
type Step = 'cycle' | 'type' | 'config';

// 
const TOTAL_DURATION_OPTIONS = [
  { label: '1', value: 30, Icon: Target },
  { label: '3', value: 90, Icon: TrendingUp },
  { label: '', value: 180, Icon: Tent },
  { label: '1', value: 365, Icon: Trophy },
];

// 
const CYCLE_LENGTH_OPTIONS = [
  { label: '10', value: 10, description: '', recommended: '<3' },
  { label: '30', value: 30, description: '', recommended: '3' },
];

// 
const TASK_TYPE_OPTIONS = [
  {
    type: 'NUMERIC' as MainlineTaskType,
    Icon: BarChart3,
    label: '',
    description: '',
    examples: '',
    feature: ''
  },
  {
    type: 'CHECKLIST' as MainlineTaskType,
    Icon: ClipboardList,
    label: '',
    description: '',
    examples: '',
    feature: ''
  },
  {
    type: 'CHECK_IN' as MainlineTaskType,
    Icon: CheckCircle,
    label: '',
    description: '',
    examples: '',
    feature: ''
  }
];

export default function CreateMainlineTaskModal({
  visible,
  onClose,
  onSubmit
}: CreateMainlineTaskModalProps) {
  // /- localStorage
  const [taskCategory, setTaskCategory] = useState<'MAINLINE' | 'SIDELINE'>('MAINLINE');
  
  const { themeColors } = useTheme();
  
  // localStorage
  useEffect(() => {
    if (visible) {
      const storedTasks = localStorage.getItem('dc_tasks');
      let hasActiveMainlineTask = false;
      
      if (storedTasks) {
        try {
          const parsedTasks = JSON.parse(storedTasks);
          hasActiveMainlineTask = parsedTasks.some(
            (t: any) => t.type === 'mainline' && t.status !== 'archived'
          );
        } catch (e) {
          console.error('dc_tasks:', e);
        }
      }
      
      setTaskCategory(hasActiveMainlineTask ? 'SIDELINE' : 'MAINLINE');
    }
  }, [visible]);
  // 
  const [currentStep, setCurrentStep] = useState<Step>('cycle');
  
  // 1
  const [totalDays, setTotalDays] = useState(90);
  const [cycleDays, setCycleDays] = useState(10);
  const [customDays, setCustomDays] = useState('');
  const [isCustom, setIsCustom] = useState(false);
  
  // 2
  const [selectedType, setSelectedType] = useState<MainlineTaskType | null>(null);
  
  // 3
  const [taskTitle, setTaskTitle] = useState('');
  
  // 
  const [numericDirection, setNumericDirection] = useState<NumericDirection>('DECREASE');
  const [numericUnit, setNumericUnit] = useState('');
  const [startValue, setStartValue] = useState('');
  const [targetValue, setTargetValue] = useState('');
  
  // 
  const [totalItems, setTotalItems] = useState('10');
  const [checklistItems, setChecklistItems] = useState<string[]>(['', '', '', '']);
  
  // 
  const [checkInUnit, setCheckInUnit] = useState<CheckInUnit>('TIMES');
  const [allowMultiple, setAllowMultiple] = useState(true); // 
  const [weekendExempt, setWeekendExempt] = useState(false);
  
  // 
  const [dailyMaxTimes, setDailyMaxTimes] = useState('1'); // 
  const [cycleTargetTimes, setCycleTargetTimes] = useState(''); // 
  
  // 
  const [dailyTargetMinutes, setDailyTargetMinutes] = useState('15'); // 
  const [cycleTargetMinutes, setCycleTargetMinutes] = useState(''); // 
  
  // 
  const [dailyTargetValue, setDailyTargetValue] = useState(''); // 
  const [cycleTargetValue, setCycleTargetValue] = useState(''); // 
  const [valueUnit, setValueUnit] = useState(''); // 
  
  // 
  const cycleInfo = useMemo(() => {
    const totalCycles = Math.floor(totalDays / cycleDays);
    const remainingDays = totalDays % cycleDays;
    return {
      totalCycles,
      remainingDays
    };
  }, [totalDays, cycleDays]);
  
  // 
  const recommendedCycleDays = totalDays < 90 ? 10 : 30;
  
  // 
  const resetForm = () => {
    setCurrentStep('cycle');
    setTotalDays(90);
    setCycleDays(10);
    setCustomDays('');
    setIsCustom(false);
    setSelectedType(null);
    setTaskTitle('');
    setNumericDirection('DECREASE');
    setNumericUnit('');
    setStartValue('');
    setTargetValue('');
    setTotalItems('10');
    setChecklistItems(['', '', '', '']);
    setCheckInUnit('TIMES');
    setAllowMultiple(true);
    setWeekendExempt(false);
    setDailyMaxTimes('1');
    setCycleTargetTimes('');
    setDailyTargetMinutes('15');
    setCycleTargetMinutes('');
    setDailyTargetValue('');
    setCycleTargetValue('');
    setValueUnit('');
  };
  
  const handleClose = () => {
    resetForm();
    onClose();
  };
  
  const handleNext = () => {
    if (currentStep === 'cycle') {
      setCurrentStep('type');
    } else if (currentStep === 'type' && selectedType) {
      setCurrentStep('config');
    }
  };
  
  const handleBack = () => {
    if (currentStep === 'type') {
      setCurrentStep('cycle');
    } else if (currentStep === 'config') {
      setCurrentStep('type');
    }
  };
  
  const handleSubmit = () => {
    if (!taskTitle.trim() || !selectedType) {
      alert('');
      return;
    }
    
    const baseData = {
      title: taskTitle,
      mainlineType: selectedType,
      taskCategory, // 
      totalDays,
      cycleDays,
      totalCycles: cycleInfo.totalCycles,
      startDate: dayjs().format('YYYY-MM-DD')
    };
    
    let taskData: any = baseData;
    
    if (selectedType === 'NUMERIC') {
      const start = parseFloat(startValue);
      const target = parseFloat(targetValue);
      if (isNaN(start) || isNaN(target)) {
        alert('');
        return;
      }
      const totalChange = Math.abs(target - start);
      taskData.numericConfig = {
        direction: numericDirection,
        unit: numericUnit,
        startValue: start,
        targetValue: target,
        currentValue: start,
        perCycleTarget: totalChange / cycleInfo.totalCycles,
        perDayAverage: totalChange / totalDays
      };
    } else if (selectedType === 'CHECKLIST') {
      const items = parseInt(totalItems);
      if (isNaN(items) || items < 1) {
        alert('');
        return;
      }
      const filledItems = checklistItems.filter(item => item.trim());
      taskData.checklistConfig = {
        totalItems: items,
        items: filledItems.map((title, index) => ({
          id: `item_${Date.now()}_${index}`,
          title,
          status: 'PENDING',
          cycle: Math.floor(index / Math.ceil(items / cycleInfo.totalCycles)) + 1
        }))
      };
    } else if (selectedType === 'CHECK_IN') {
      // 
      const checkInConfig: any = {
        unit: checkInUnit,
        allowMultiplePerDay: allowMultiple,
        weekendExempt: weekendExempt,
        currentStreak: 0,
        longestStreak: 0,
        checkInRate: 0,
        streaks: [],
        records: []
      };
      
      if (checkInUnit === 'TIMES') {
        // 
        const maxTimes = parseInt(dailyMaxTimes) || 1;
        const defaultCycleTarget = cycleDays * maxTimes;
        const cycleTarget = cycleTargetTimes ? parseInt(cycleTargetTimes) : defaultCycleTarget;
        
        checkInConfig.dailyMaxTimes = maxTimes;
        checkInConfig.cycleTargetTimes = cycleTarget;
        checkInConfig.perCycleTarget = cycleTarget;
      } else if (checkInUnit === 'DURATION') {
        // 
        const dailyMinutes = parseInt(dailyTargetMinutes) || 15;
        const defaultCycleMinutes = cycleDays * dailyMinutes;
        const cycleMinutes = cycleTargetMinutes ? parseInt(cycleTargetMinutes) : defaultCycleMinutes;
        
        checkInConfig.dailyTargetMinutes = dailyMinutes;
        checkInConfig.cycleTargetMinutes = cycleMinutes;
        checkInConfig.quickDurations = [5, 10, 15];
        checkInConfig.perCycleTarget = cycleMinutes;
      } else if (checkInUnit === 'QUANTITY') {
        // 
        const dailyValue = parseFloat(dailyTargetValue) || 0;
        const defaultCycleValue = cycleDays * dailyValue;
        const cycleValue = cycleTargetValue ? parseFloat(cycleTargetValue) : defaultCycleValue;
        
        if (!dailyValue) {
          alert('');
          return;
        }
        
        checkInConfig.dailyTargetValue = dailyValue;
        checkInConfig.cycleTargetValue = cycleValue;
        checkInConfig.valueUnit = valueUnit;
        checkInConfig.perCycleTarget = cycleValue;
      }
      
      taskData.checkInConfig = checkInConfig;
    }
    
    onSubmit(taskData);
    handleClose();
  };
  
  // 1
  const renderCycleStep = () => (
    <div style={{ padding: '24px' }}>
      <h2 style={{ fontSize: '20px', fontWeight: '600', marginBottom: '24px' }}>
        1
      </h2>
      
      {/*  */}
      <div style={{ marginBottom: '24px' }}>
        <div style={{ fontSize: '14px', color: '#666', marginBottom: '12px', fontWeight: '500' }}>
          <Calendar size={16} style={{ display: 'inline', marginRight: '6px' }} /> 
        </div>
        <div style={{ fontSize: '13px', color: '#999', marginBottom: '12px' }}>
          
        </div>
        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '8px', marginBottom: '12px' }}>
          {TOTAL_DURATION_OPTIONS.map(option => (
            <button
              key={option.value}
              onClick={() => {
                setTotalDays(option.value);
                setIsCustom(false);
                // 
                setCycleDays(option.value < 90 ? 10 : 30);
              }}
              style={{
                padding: '16px',
                backgroundColor: !isCustom && totalDays === option.value ? 'black' : 'white',
                color: !isCustom && totalDays === option.value ? 'white' : 'black',
                border: '1px solid #e5e5e5',
                borderRadius: '12px',
                cursor: 'pointer',
                textAlign: 'left',
                transition: 'all 0.2s'
              }}
            >
              <option.Icon size={24} style={{ marginBottom: '4px' }} />
              <div style={{ fontWeight: '600', fontSize: '14px' }}>{option.label}</div>
            </button>
          ))}
        </div>
        
        {/*  */}
        <div>
          <div style={{ fontSize: '13px', color: '#666', marginBottom: '8px' }}></div>
          <input
            type="number"
            value={customDays}
            onChange={(e) => {
              setCustomDays(e.target.value);
              const days = parseInt(e.target.value);
              if (days > 0) {
                setTotalDays(days);
                setIsCustom(true);
                setCycleDays(days < 90 ? 10 : 30);
              }
            }}
            placeholder=""
            style={{
              width: '100%',
              padding: '12px',
              border: '1px solid #e5e5e5',
              borderRadius: '12px',
              fontSize: '14px',
              outline: 'none',
              boxSizing: 'border-box'
            }}
          />
        </div>
      </div>
      
      {/*  */}
      <div style={{ marginBottom: '24px' }}>
        <div style={{ fontSize: '14px', color: '#666', marginBottom: '12px', fontWeight: '500' }}>
          <Target size={16} style={{ display: 'inline', marginRight: '6px' }} /> 
        </div>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
          {CYCLE_LENGTH_OPTIONS.map(option => (
            <button
              key={option.value}
              onClick={() => setCycleDays(option.value)}
              style={{
                padding: '16px',
                backgroundColor: cycleDays === option.value ? 'black' : 'white',
                color: cycleDays === option.value ? 'white' : 'black',
                border: '1px solid #e5e5e5',
                borderRadius: '12px',
                cursor: 'pointer',
                textAlign: 'left',
                transition: 'all 0.2s',
                position: 'relative'
              }}
            >
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <div>
                  <div style={{ fontWeight: '600', fontSize: '15px', marginBottom: '4px' }}>
                    {option.label}
                  </div>
                  <div style={{ fontSize: '12px', opacity: 0.7 }}>
                    {option.description}
                  </div>
                </div>
                {option.value === recommendedCycleDays && (
                  <span style={{
                    padding: '4px 8px',
                    backgroundColor: cycleDays === option.value ? 'rgba(255,255,255,0.2)' : '#fff5f0',
                    color: cycleDays === option.value ? 'white' : '#ff6b6b',
                    borderRadius: '6px',
                    fontSize: '11px',
                    fontWeight: '600'
                  }}>
                    
                  </span>
                )}
              </div>
            </button>
          ))}
        </div>
      </div>
      
      {/*  */}
      <div style={{
        backgroundColor: '#f0f7ff',
        border: '1px solid #4a9eff',
        borderRadius: '12px',
        padding: '16px'
      }}>
        <div style={{ fontSize: '14px', fontWeight: '600', marginBottom: '12px', display: 'flex', alignItems: 'center', gap: '6px' }}>
          <span></span>
          <span> {cycleInfo.totalCycles} </span>
        </div>
        <div style={{ fontSize: '12px', color: '#666', lineHeight: '1.6' }}>
           {totalDays}<br/>
           {cycleDays}<br/>
           {cycleInfo.totalCycles}<br/>
          {cycleInfo.remainingDays > 0 && ` ${cycleInfo.remainingDays}`}
        </div>
      </div>
    </div>
  );
  
  // 2
  const renderTypeStep = () => (
    <div style={{ padding: '24px' }}>
      <h2 style={{ fontSize: '20px', fontWeight: '600', marginBottom: '24px' }}>
        2
      </h2>
      
      <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
        {TASK_TYPE_OPTIONS.map(option => (
          <button
            key={option.type}
            onClick={() => setSelectedType(option.type)}
            style={{
              padding: '20px',
              backgroundColor: selectedType === option.type ? '#f0f7ff' : 'white',
              border: selectedType === option.type ? '2px solid #4a9eff' : '1px solid #e5e5e5',
              borderRadius: '16px',
              cursor: 'pointer',
              textAlign: 'left',
              transition: 'all 0.2s'
            }}
          >
            <div style={{ display: 'flex', alignItems: 'flex-start', gap: '12px' }}>
              <option.Icon size={32} />
              <div style={{ flex: 1 }}>
                <div style={{ fontSize: '16px', fontWeight: '600', marginBottom: '6px' }}>
                  {option.label}
                </div>
                <div style={{ fontSize: '13px', color: '#666', marginBottom: '4px' }}>
                  {option.description}
                </div>
                <div style={{ fontSize: '12px', color: '#999', marginBottom: '4px' }}>
                  {option.examples}
                </div>
                <div style={{ fontSize: '12px', color: '#4a9eff', fontWeight: '500' }}>
                  {option.feature}
                </div>
              </div>
              {selectedType === option.type && (
                <CheckCircle size={24} color="#4a9eff" />
              )}
            </div>
          </button>
        ))}
      </div>
    </div>
  );
  
  // 3
  const renderConfigStep = () => {
    if (!selectedType) return null;
    
    return (
      <div style={{ padding: '24px' }}>
        <h2 style={{ fontSize: '20px', fontWeight: '600', marginBottom: '24px' }}>
          3{TASK_TYPE_OPTIONS.find(t => t.type === selectedType)?.label}
        </h2>
        
        {/*  */}
        <div style={{ marginBottom: '20px' }}>
          <div style={{ fontSize: '14px', color: '#666', marginBottom: '8px', fontWeight: '500' }}>
             
          </div>
          <input
            type="text"
            value={taskTitle}
            onChange={(e) => setTaskTitle(e.target.value)}
            placeholder={
              selectedType === 'NUMERIC' ? '' :
              selectedType === 'CHECKLIST' ? '10' :
              '20'
            }
            style={{
              width: '100%',
              padding: '12px',
              border: '1px solid #e5e5e5',
              borderRadius: '12px',
              fontSize: '14px',
              outline: 'none',
              boxSizing: 'border-box'
            }}
          />
        </div>
        
        {/*  */}
        {selectedType === 'NUMERIC' && (
          <>
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontSize: '14px', color: '#666', marginBottom: '12px', fontWeight: '500' }}>
                 
              </div>
              <div style={{ display: 'flex', gap: '12px', marginBottom: '12px' }}>
                <div style={{ flex: 1 }}>
                  <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                  <input
                    type="number"
                    value={startValue}
                    onChange={(e) => setStartValue(e.target.value)}
                    placeholder="150"
                    style={{
                      width: '100%',
                      padding: '12px',
                      border: '1px solid #e5e5e5',
                      borderRadius: '12px',
                      fontSize: '14px',
                      outline: 'none',
                      boxSizing: 'border-box'
                    }}
                  />
                </div>
                <div style={{ flex: 1 }}>
                  <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                  <input
                    type="number"
                    value={targetValue}
                    onChange={(e) => setTargetValue(e.target.value)}
                    placeholder="140"
                    style={{
                      width: '100%',
                      padding: '12px',
                      border: '1px solid #e5e5e5',
                      borderRadius: '12px',
                      fontSize: '14px',
                      outline: 'none',
                      boxSizing: 'border-box'
                    }}
                  />
                </div>
              </div>
              <div style={{ marginBottom: '12px' }}>
                <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                <input
                  type="text"
                  value={numericUnit}
                  onChange={(e) => setNumericUnit(e.target.value)}
                  placeholder=""
                  style={{
                    width: '100%',
                    padding: '12px',
                    border: '1px solid #e5e5e5',
                    borderRadius: '12px',
                    fontSize: '14px',
                    outline: 'none',
                    boxSizing: 'border-box'
                  }}
                />
              </div>
              <div>
                <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                <div style={{ display: 'flex', gap: '8px' }}>
                  <button
                    onClick={() => setNumericDirection('INCREASE')}
                    style={{
                      flex: 1,
                      padding: '12px',
                      backgroundColor: numericDirection === 'INCREASE' ? 'black' : 'white',
                      color: numericDirection === 'INCREASE' ? 'white' : 'black',
                      border: '1px solid #e5e5e5',
                      borderRadius: '12px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                    
                  </button>
                  <button
                    onClick={() => setNumericDirection('DECREASE')}
                    style={{
                      flex: 1,
                      padding: '12px',
                      backgroundColor: numericDirection === 'DECREASE' ? 'black' : 'white',
                      color: numericDirection === 'DECREASE' ? 'white' : 'black',
                      border: '1px solid #e5e5e5',
                      borderRadius: '12px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                    
                  </button>
                </div>
              </div>
            </div>
            
            {/*  */}
            {startValue && targetValue && (
              <div style={{
                backgroundColor: '#f0f7ff',
                border: '1px solid #4a9eff',
                borderRadius: '12px',
                padding: '16px',
                marginBottom: '20px'
              }}>
                <div style={{ fontSize: '14px', fontWeight: '600', marginBottom: '12px' }}>
                   
                </div>
                <div style={{ fontSize: '12px', color: '#666', lineHeight: '1.8' }}>
                   {numericDirection === 'DECREASE' ? '' : ''} {Math.abs(parseFloat(targetValue) - parseFloat(startValue)).toFixed(2)}{numericUnit}<br/>
                   {(Math.abs(parseFloat(targetValue) - parseFloat(startValue)) / cycleInfo.totalCycles).toFixed(2)} {numericUnit}/<br/>
                   {(Math.abs(parseFloat(targetValue) - parseFloat(startValue)) / totalDays).toFixed(2)} {numericUnit}/
                </div>
              </div>
            )}
          </>
        )}
        
        {/*  */}
        {selectedType === 'CHECKLIST' && (
          <>
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontSize: '14px', color: '#666', marginBottom: '12px', fontWeight: '500' }}>
                 
              </div>
              <div style={{ marginBottom: '12px' }}>
                <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                <input
                  type="number"
                  value={totalItems}
                  onChange={(e) => setTotalItems(e.target.value)}
                  placeholder="10"
                  style={{
                    width: '100%',
                    padding: '12px',
                    border: '1px solid #e5e5e5',
                    borderRadius: '12px',
                    fontSize: '14px',
                    outline: 'none',
                    boxSizing: 'border-box'
                  }}
                />
              </div>
              
              <div style={{ fontSize: '12px', color: '#999', marginBottom: '8px' }}>
                
              </div>
              {checklistItems.map((item, index) => (
                <input
                  key={index}
                  type="text"
                  value={item}
                  onChange={(e) => {
                    const newItems = [...checklistItems];
                    newItems[index] = e.target.value;
                    setChecklistItems(newItems);
                  }}
                  placeholder={`${index + 1}. `}
                  style={{
                    width: '100%',
                    padding: '10px 12px',
                    border: '1px solid #e5e5e5',
                    borderRadius: '8px',
                    fontSize: '13px',
                    outline: 'none',
                    boxSizing: 'border-box',
                    marginBottom: '8px'
                  }}
                />
              ))}
              <button
                onClick={() => setChecklistItems([...checklistItems, ''])}
                style={{
                  width: '100%',
                  padding: '10px',
                  backgroundColor: 'white',
                  border: '1px dashed #ccc',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  fontSize: '13px',
                  color: '#666'
                }}
              >
                + 
              </button>
            </div>
            
            {/*  */}
            {totalItems && (
              <div style={{
                backgroundColor: '#f0f7ff',
                border: '1px solid #4a9eff',
                borderRadius: '12px',
                padding: '16px'
              }}>
                <div style={{ fontSize: '14px', fontWeight: '600', marginBottom: '12px' }}>
                   
                </div>
                <div style={{ fontSize: '12px', color: '#666', lineHeight: '1.8' }}>
                   {totalItems}<br/>
                   {Math.ceil(parseInt(totalItems) / cycleInfo.totalCycles)}/
                </div>
              </div>
            )}
          </>
        )}
        
        {/*  */}
        {selectedType === 'CHECK_IN' && (
          <>
            <div style={{ marginBottom: '20px' }}>
              <div style={{ fontSize: '14px', color: '#666', marginBottom: '12px', fontWeight: '500' }}>
                 
              </div>
              <div style={{ display: 'flex', gap: '8px', marginBottom: '16px' }}>
                {[
                  { value: 'TIMES', label: '', desc: '' },
                  { value: 'DURATION', label: '', desc: '' },
                  { value: 'QUANTITY', label: '', desc: '' }
                ].map(option => (
                  <button
                    key={option.value}
                    onClick={() => setCheckInUnit(option.value as CheckInUnit)}
                    style={{
                      flex: 1,
                      padding: '12px 8px',
                      backgroundColor: checkInUnit === option.value ? 'black' : 'white',
                      color: checkInUnit === option.value ? 'white' : 'black',
                      border: '1px solid #e5e5e5',
                      borderRadius: '10px',
                      cursor: 'pointer',
                      textAlign: 'center'
                    }}
                  >
                    <div style={{ fontSize: '14px', fontWeight: '600' }}>{option.label}</div>
                    <div style={{ fontSize: '11px', opacity: 0.7, marginTop: '2px' }}>{option.desc}</div>
                  </button>
                ))}
              </div>
              
              {/*  */}
              {checkInUnit === 'TIMES' && (
                <div style={{ marginBottom: '16px' }}>
                  <div style={{ fontSize: '13px', color: '#666', marginBottom: '8px', fontWeight: '500' }}>
                     
                  </div>
                  <div style={{ display: 'flex', gap: '12px', marginBottom: '12px' }}>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                      <input
                        type="number"
                        value={dailyMaxTimes}
                        onChange={(e) => setDailyMaxTimes(e.target.value)}
                        placeholder="1"
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #e5e5e5',
                          borderRadius: '12px',
                          fontSize: '14px',
                          outline: 'none',
                          boxSizing: 'border-box'
                        }}
                      />
                    </div>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                      <input
                        type="number"
                        value={cycleTargetTimes}
                        onChange={(e) => setCycleTargetTimes(e.target.value)}
                        placeholder={`${cycleDays * (parseInt(dailyMaxTimes) || 1)}`}
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #e5e5e5',
                          borderRadius: '12px',
                          fontSize: '14px',
                          outline: 'none',
                          boxSizing: 'border-box'
                        }}
                      />
                    </div>
                  </div>
                  <div style={{ fontSize: '11px', color: '#999' }}>
                     =    = {cycleDays * (parseInt(dailyMaxTimes) || 1)} 
                  </div>
                </div>
              )}
              
              {/*  */}
              {checkInUnit === 'DURATION' && (
                <div style={{ marginBottom: '16px' }}>
                  <div style={{ fontSize: '13px', color: '#666', marginBottom: '8px', fontWeight: '500' }}>
                     
                  </div>
                  <div style={{ display: 'flex', gap: '12px', marginBottom: '12px' }}>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}>()</div>
                      <input
                        type="number"
                        value={dailyTargetMinutes}
                        onChange={(e) => setDailyTargetMinutes(e.target.value)}
                        placeholder="15"
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #e5e5e5',
                          borderRadius: '12px',
                          fontSize: '14px',
                          outline: 'none',
                          boxSizing: 'border-box'
                        }}
                      />
                    </div>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}>()</div>
                      <input
                        type="number"
                        value={cycleTargetMinutes}
                        onChange={(e) => setCycleTargetMinutes(e.target.value)}
                        placeholder={`${cycleDays * (parseInt(dailyTargetMinutes) || 15)}`}
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #e5e5e5',
                          borderRadius: '12px',
                          fontSize: '14px',
                          outline: 'none',
                          boxSizing: 'border-box'
                        }}
                      />
                    </div>
                  </div>
                  <div style={{ fontSize: '11px', color: '#999' }}>
                     5/10/15 
                  </div>
                </div>
              )}
              
              {/*  */}
              {checkInUnit === 'QUANTITY' && (
                <div style={{ marginBottom: '16px' }}>
                  <div style={{ fontSize: '13px', color: '#666', marginBottom: '8px', fontWeight: '500' }}>
                     
                  </div>
                  <div style={{ display: 'flex', gap: '12px', marginBottom: '12px' }}>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                      <input
                        type="number"
                        value={dailyTargetValue}
                        onChange={(e) => setDailyTargetValue(e.target.value)}
                        placeholder="10"
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #e5e5e5',
                          borderRadius: '12px',
                          fontSize: '14px',
                          outline: 'none',
                          boxSizing: 'border-box'
                        }}
                      />
                    </div>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                      <input
                        type="text"
                        value={valueUnit}
                        onChange={(e) => setValueUnit(e.target.value)}
                        placeholder=""
                        style={{
                          width: '100%',
                          padding: '12px',
                          border: '1px solid #e5e5e5',
                          borderRadius: '12px',
                          fontSize: '14px',
                          outline: 'none',
                          boxSizing: 'border-box'
                        }}
                      />
                    </div>
                  </div>
                  <div style={{ marginBottom: '12px' }}>
                    <div style={{ fontSize: '12px', color: '#999', marginBottom: '6px' }}></div>
                    <input
                      type="number"
                      value={cycleTargetValue}
                      onChange={(e) => setCycleTargetValue(e.target.value)}
                      placeholder={`${cycleDays * (parseFloat(dailyTargetValue) || 0)}`}
                      style={{
                        width: '100%',
                        padding: '12px',
                        border: '1px solid #e5e5e5',
                        borderRadius: '12px',
                        fontSize: '14px',
                        outline: 'none',
                        boxSizing: 'border-box'
                      }}
                    />
                  </div>
                </div>
              )}
              
              {/*  */}
              <div style={{
                backgroundColor: '#f8f8f8',
                borderRadius: '12px',
                padding: '12px'
              }}>
                <div style={{ fontSize: '12px', color: '#666', marginBottom: '8px', fontWeight: '500' }}>
                  
                </div>
                <label style={{ display: 'flex', alignItems: 'center', marginBottom: '8px', cursor: 'pointer' }}>
                  <input
                    type="checkbox"
                    checked={allowMultiple}
                    onChange={(e) => setAllowMultiple(e.target.checked)}
                    style={{ marginRight: '8px' }}
                  />
                  <span style={{ fontSize: '13px' }}></span>
                </label>
                <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>
                  <input
                    type="checkbox"
                    checked={weekendExempt}
                    onChange={(e) => setWeekendExempt(e.target.checked)}
                    style={{ marginRight: '8px' }}
                  />
                  <span style={{ fontSize: '13px' }}></span>
                </label>
              </div>
            </div>
            
            {/*  */}
            <div style={{
              backgroundColor: '#f0f7ff',
              border: '1px solid #4a9eff',
              borderRadius: '12px',
              padding: '16px'
            }}>
              <div style={{ fontSize: '14px', fontWeight: '600', marginBottom: '12px' }}>
                 
              </div>
              <div style={{ fontSize: '12px', color: '#666', lineHeight: '1.8' }}>
                {checkInUnit === 'TIMES' && (
                  <>
                     {parseInt(dailyMaxTimes) || 1} <br/>
                     {cycleTargetTimes || (cycleDays * (parseInt(dailyMaxTimes) || 1))} <br/>
                     {cycleInfo.totalCycles * (parseInt(cycleTargetTimes) || (cycleDays * (parseInt(dailyMaxTimes) || 1)))} 
                  </>
                )}
                {checkInUnit === 'DURATION' && (
                  <>
                     {parseInt(dailyTargetMinutes) || 15} <br/>
                     {cycleTargetMinutes || (cycleDays * (parseInt(dailyTargetMinutes) || 15))} <br/>
                     {cycleInfo.totalCycles * (parseInt(cycleTargetMinutes) || (cycleDays * (parseInt(dailyTargetMinutes) || 15)))} 
                  </>
                )}
                {checkInUnit === 'QUANTITY' && dailyTargetValue && (
                  <>
                     {parseFloat(dailyTargetValue)} {valueUnit}<br/>
                     {cycleTargetValue || (cycleDays * parseFloat(dailyTargetValue))} {valueUnit}<br/>
                     {cycleInfo.totalCycles * (parseFloat(cycleTargetValue) || (cycleDays * parseFloat(dailyTargetValue)))} {valueUnit}
                  </>
                )}
              </div>
            </div>
          </>
        )}
      </div>
    );
  };
  
  return (
    <Popup
      visible={visible}
      onMaskClick={handleClose}
      position='bottom'
      destroyOnClose={false}
      bodyStyle={{
        borderTopLeftRadius: '24px',
        borderTopRightRadius: '24px',
        minHeight: '80vh',
        maxHeight: '90vh',
        overflowY: 'auto',
        background: '#ffffff'
      }}
    >
      {/* Header */}
      <div style={{
        padding: '16px 24px',
        borderBottom: '1px solid #f0f0f0',
        position: 'sticky',
        top: 0,
        backgroundColor: 'white',
        zIndex: 10
      }}>
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between'
        }}>
          <h2 style={{ fontSize: '18px', fontWeight: '600', margin: 0 }}>
            {taskCategory === 'MAINLINE' ? '' : ''}
          </h2>
          <button
            onClick={handleClose}
            style={{
              width: '32px',
              height: '32px',
              border: 'none',
              backgroundColor: 'transparent',
              cursor: 'pointer',
              fontSize: '20px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        </div>
        
        {/*  */}
        <div style={{
          display: 'flex',
          alignItems: 'center',
          gap: '8px',
          marginTop: '16px'
        }}>
          {['cycle', 'type', 'config'].map((step, index) => (
            <div key={step} style={{ display: 'flex', alignItems: 'center', flex: 1 }}>
              <div style={{
                width: '24px',
                height: '24px',
                borderRadius: '50%',
                backgroundColor: currentStep === step || (
                  (step === 'type' && currentStep === 'config') ||
                  (step === 'cycle' && (currentStep === 'type' || currentStep === 'config'))
                ) ? 'black' : '#e5e5e5',
                color: currentStep === step || (
                  (step === 'type' && currentStep === 'config') ||
                  (step === 'cycle' && (currentStep === 'type' || currentStep === 'config'))
                ) ? 'white' : '#999',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '12px',
                fontWeight: '600'
              }}>
                {index + 1}
              </div>
              {index < 2 && (
                <div style={{
                  flex: 1,
                  height: '2px',
                  backgroundColor: (
                    (step === 'cycle' && (currentStep === 'type' || currentStep === 'config'))
                  ) ? 'black' : '#e5e5e5',
                  marginLeft: '8px'
                }} />
              )}
            </div>
          ))}
        </div>
      </div>
      
      {/* Content */}
      <div style={{ paddingBottom: '80px' }}>
        {currentStep === 'cycle' && renderCycleStep()}
        {currentStep === 'type' && renderTypeStep()}
        {currentStep === 'config' && renderConfigStep()}
      </div>
      
      {/* Footer */}
      <div style={{
        padding: '16px 24px',
        backgroundColor: 'white',
        borderTop: '1px solid #f0f0f0',
        position: 'sticky',
        bottom: 0,
        display: 'flex',
        gap: '12px'
      }}>
        {currentStep !== 'cycle' && (
          <button
            onClick={handleBack}
            style={{
              flex: 1,
              padding: '14px',
              backgroundColor: 'white',
              color: 'black',
              border: '1px solid #e5e5e5',
              borderRadius: '12px',
              fontSize: '15px',
              fontWeight: '600',
              cursor: 'pointer'
            }}
          >
            
          </button>
        )}
        <button
          onClick={currentStep === 'config' ? handleSubmit : handleNext}
          disabled={currentStep === 'type' && !selectedType}
          style={{
            flex: 1,
            padding: '14px',
            backgroundColor: (currentStep === 'type' && !selectedType) ? '#ccc' : themeColors.primary,
            color: 'white',
            border: 'none',
            borderRadius: '12px',
            fontSize: '15px',
            fontWeight: '600',
            cursor: (currentStep === 'type' && !selectedType) ? 'not-allowed' : 'pointer'
          }}
        >
          {currentStep === 'config' ? '' : ''}
        </button>
      </div>
    </Popup>
  );
}
