import { useState } from 'react';
import dayjs from 'dayjs';
import { Plus, X, Archive, Settings as SettingsIcon } from 'lucide-react';
// import CreateGoalModal from './dc/CreateGoalModal';
import CreateMainlineTaskModal from './dc/CreateMainlineTaskModal';
import { MainlineTaskCard, SidelineTaskCard } from './dc/card';
import GoalDetailModal from './dc/detail';
import { Task, MainlineTask } from './dc/types';
import VacationContent from './dc/happy/VacationContent';
import { TaskProvider, useTaskContext } from './dc/context';
import ArchiveList from './dc/archive';
import Settings from './dc/settings';
import { ThemeProvider } from './dc/settings/theme';

function DemoPageContent() {
  const { tasks, addTask, refreshTasks } = useTaskContext();
  const [modalVisible, setModalVisible] = useState(false);
  const [mainlineModalVisible, setMainlineModalVisible] = useState(false);
  const [showAllSidelineTasks, setShowAllSidelineTasks] = useState(false);
  const [selectedTaskId, setSelectedTaskId] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<'normal' | 'vacation' | 'memorial'>('normal');
  const [showArchive, setShowArchive] = useState(false);
  const [showSettings, setShowSettings] = useState(false);

  // 
  const activeTasks = tasks.filter(t => (t as any).status !== 'archived');

  // 
  const hasMainOrSubTasks = activeTasks.some(t => t.type === 'mainline' || t.type === 'sidelineA' || t.type === 'sidelineB');

  // 
  const sidelineTasks = activeTasks.filter(t => t.type === 'sidelineA' || t.type === 'sidelineB');
  const displayedSidelineTasks = sidelineTasks.slice(0, 3);

  // 
  const hasMainlineTask = activeTasks.some(t => t.type === 'mainline');

  // 
  const handleCreateTask = (taskData: any) => {
    const today = dayjs().format('YYYY-MM-DD');
    const isMainline = taskData.taskCategory === 'MAINLINE';
    
    // 
    const task: MainlineTask = {
      id: Date.now().toString(),
      mainlineType: taskData.mainlineType,
      title: taskData.title,
      status: 'ACTIVE',
      createdAt: today,
      cycleConfig: {
        totalDurationDays: taskData.totalDays,
        cycleLengthDays: taskData.cycleDays,
        totalCycles: taskData.totalCycles,
        currentCycle: 1
      },
      progress: {
        totalPercentage: 0,
        currentCyclePercentage: 0
      },
      numericConfig: taskData.numericConfig,
      checklistConfig: taskData.checklistConfig ? {
        ...taskData.checklistConfig,
        completedItems: 0,
        perCycleTarget: Math.ceil(taskData.checklistConfig.totalItems / taskData.totalCycles)
      } : undefined,
      checkInConfig: taskData.checkInConfig ? {
        ...taskData.checkInConfig,
        currentStreak: 0,
        longestStreak: 0,
        checkInRate: 0,
        streaks: [],
        records: []
      } : undefined,
      history: []
    };

    //  Task 
    const newTask: Task = {
      id: task.id,
      title: taskData.title,
      progress: 0,
      currentDay: 0,
      totalDays: taskData.totalDays,
      type: isMainline ? 'mainline' : 'sidelineA',
      mainlineType: taskData.mainlineType,
      mainlineTask: task,
      startDate: today,
      cycleDays: taskData.cycleDays,
      totalCycles: taskData.totalCycles,
      cycle: `1/${taskData.totalCycles}`
    };

    addTask(newTask);
    setMainlineModalVisible(false);
  };

  // 
  const handleAddClick = () => {
    setMainlineModalVisible(true);
  };

  return (
    <div style={{
      width: '100%',
      minWidth: '390px',
      height: '100vh',
      backgroundColor: 'white',
      borderRadius: '40px',
      overflow: 'hidden',
      display: 'flex',
      flexDirection: 'column',
      margin: '0 auto'
    }}>
      {/* Header */}
      <div style={{
        padding: '16px 24px',
        backgroundColor: 'white',
        borderBottom: '1px solid #f0f0f0'
      }}>
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between'
        }}>
          {/* TAB */}
          <div style={{ display: 'flex', alignItems: 'center', gap: '24px' }}>
            {[
              { key: 'normal', label: '' },
              { key: 'vacation', label: '' },
              { key: 'memorial', label: '' }
            ].map((tab) => (
              <button
                key={tab.key}
                onClick={() => setActiveTab(tab.key as 'normal' | 'vacation' | 'memorial')}
                style={{
                  position: 'relative',
                  padding: '4px 0',
                  border: 'none',
                  backgroundColor: 'transparent',
                  fontSize: '15px',
                  fontWeight: activeTab === tab.key ? '500' : '400',
                  color: activeTab === tab.key ? '#000' : '#999',
                  cursor: 'pointer',
                  transition: 'color 0.2s'
                }}
              >
                {tab.label}
                {/*  */}
                <span style={{
                  position: 'absolute',
                  bottom: '-2px',
                  left: '50%',
                  transform: 'translateX(-50%)',
                  width: activeTab === tab.key ? '100%' : '0',
                  height: '2px',
                  backgroundColor: '#000',
                  borderRadius: '1px',
                  transition: 'width 0.2s ease'
                }} />
              </button>
            ))}
          </div>

          {/*  */}
          <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
            {/*  */}
            <button 
              onClick={() => setShowArchive(true)}
              style={{
                width: '32px',
                height: '32px',
                borderRadius: '8px',
                border: 'none',
                backgroundColor: 'transparent',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
                transition: 'background-color 0.2s'
              }}
              onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f5f5f5'}
              onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
              title=""
            >
              <Archive size={18} />
            </button>
            {/*  */}
            <button 
              onClick={handleAddClick}
              style={{
                width: '32px',
                height: '32px',
                borderRadius: '8px',
                border: 'none',
                backgroundColor: 'transparent',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
                transition: 'background-color 0.2s'
              }}
              onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f5f5f5'}
              onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
            >
              <Plus size={18} />
            </button>
            {/*  */}
            <button 
              onClick={() => setShowSettings(true)}
              style={{
                width: '32px',
                height: '32px',
                borderRadius: '8px',
                border: 'none',
                backgroundColor: 'transparent',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
                transition: 'background-color 0.2s'
              }}
              onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f5f5f5'}
              onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
              title=""
            >
              <SettingsIcon size={18} />
            </button>
          </div>
        </div>
      </div>

      {/* Empty State -  */}
      {/* {!isVacationMode && !hasMainOrSubTasks && (
        <div 
          onClick={handleAddClick}
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            backgroundColor: 'white',
            cursor: 'pointer',
            zIndex: 1
          }}
        >
          <img 
            src="https://img.alicdn.com/imgextra/i4/O1CN01yTnklC1ia4tDwlksJ_!!6000000004428-2-tps-2528-1696.png"
            alt=""
            style={{
              maxWidth: '90%',
              maxHeight: '90%',
              objectFit: 'contain'
            }}
          />
        </div>
      )} */}

      {/* Content */}
      <div style={{
        flex: 1,
        overflowY: 'auto',
        padding: '12px 16px'
      }}>
        {activeTab === 'vacation' ? (
          // 
          <VacationContent onAddClick={handleAddClick} />
        ) : activeTab === 'memorial' ? (
          //  - 
          <div style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            height: '60vh',
            color: '#999'
          }}>
            <div style={{
              fontSize: '48px',
              marginBottom: '16px'
            }}></div>
            <div style={{
              fontSize: '16px',
              fontWeight: '500',
              marginBottom: '8px',
              color: '#666'
            }}></div>
            <div style={{
              fontSize: '14px',
              color: '#999'
            }}></div>
          </div>
        ) : (
          // 
          <>
            {/* Cute Ghost Character */}
            <div style={{
              display: 'flex',
              justifyContent: 'center',
              marginBottom: '16px'
            }}>
              <img 
                src="https://img.alicdn.com/imgextra/i4/O1CN01FgLcMT1COZEIxZ3nG_!!6000000000071-2-tps-1248-832.png" 
                alt=""
                style={{
                  width: '100%',
                  maxWidth: '350px',
                  height: 'auto',
                  objectFit: 'contain'
                }}
              />
            </div>

            {/* Main Task Section */}
            <div style={{ marginBottom: '16px' }}>
              <div style={{ padding: '0 8px', marginBottom: '8px' }}>
                <h2 style={{
                  fontSize: '12px',
                  color: '#999',
                  textTransform: 'uppercase',
                  letterSpacing: '0.05em',
                  margin: 0,
                  fontWeight: 'normal'
                }}></h2>
              </div>
              
              {hasMainlineTask ? (
                activeTasks.filter(t => t.type === 'mainline').map(task => (
                  <MainlineTaskCard 
                    key={task.id} 
                    task={task}
                    onClick={() => setSelectedTaskId(task.id)}
                  />
                ))
              ) : (
                <div
                  onClick={() => setMainlineModalVisible(true)}
                  style={{
                    backgroundColor: 'white',
                    borderRadius: '12px',
                    padding: '16px',
                    border: '1px solid rgba(55, 53, 47, 0.09)',
                    cursor: 'pointer',
                    transition: 'all 0.2s',
                    position: 'relative',
                    overflow: 'hidden',
                    minHeight: '120px'
                  }}
                >
                  <img 
                    src="https://img.alicdn.com/imgextra/i4/O1CN01yTnklC1ia4tDwlksJ_!!6000000004428-2-tps-2528-1696.png"
                    alt=""
                    style={{
                      width: '90%',
                      height: 'auto',
                      opacity: 0.3,
                      position: 'absolute',
                      right: '-60px',
                      top: '48%',
                      transform: 'translateY(-50%)'
                    }}
                  />
                  {/*  */}
                  <div style={{ position: 'relative', zIndex: 1, width: '50%' }}>
                    {/*  */}
                    <div style={{
                      width: '100%',
                      height: '14px',
                      backgroundColor: 'rgba(55, 53, 47, 0.08)',
                      borderRadius: '4px',
                      marginBottom: '16px'
                    }}></div>
                    {/*  */}
                    <div style={{
                      width: '70%',
                      height: '10px',
                      backgroundColor: 'rgba(55, 53, 47, 0.06)',
                      borderRadius: '4px',
                      marginBottom: '10px'
                    }}></div>
                    {/*  */}
                    <div style={{
                      width: '100%',
                      height: '4px',
                      backgroundColor: 'rgba(55, 53, 47, 0.06)',
                      borderRadius: '2px',
                      marginBottom: '16px'
                    }}></div>
                    {/*  */}
                    <div style={{
                      width: '60%',
                      height: '10px',
                      backgroundColor: 'rgba(55, 53, 47, 0.06)',
                      borderRadius: '4px',
                      marginBottom: '8px'
                    }}></div>
                    {/*  */}
                    <div style={{
                      width: '40%',
                      height: '3px',
                      backgroundColor: 'rgba(55, 53, 47, 0.04)',
                      borderRadius: '2px'
                    }}></div>
                  </div>
                </div>
              )}
            </div>

            {/* Sub Tasks Section */}
            <div>
              <div style={{ padding: '0 8px', marginBottom: '8px' }}>
                <h2 style={{
                  fontSize: '12px',
                  color: '#999',
                  textTransform: 'uppercase',
                  letterSpacing: '0.05em',
                  margin: 0,
                  fontWeight: 'normal'
                }}></h2>
              </div>
              
              <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
                {displayedSidelineTasks.map(task => (
                  <SidelineTaskCard 
                    key={task.id} 
                    task={task}
                    onClick={() => setSelectedTaskId(task.id)}
                  />
                ))}
                
                {/* Show More Button */}
                {sidelineTasks.length > 3 && (
                  <button
                    onClick={() => setShowAllSidelineTasks(true)}
                    style={{
                      backgroundColor: 'white',
                      borderRadius: '12px',
                      padding: '12px 16px',
                      border: '1px solid #f0f0f0',
                      cursor: 'pointer',
                      transition: 'background-color 0.2s',
                      fontSize: '14px',
                      color: '#666',
                      textAlign: 'center'
                    }}
                    onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#fafafa'}
                    onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'white'}
                  >
                     ({sidelineTasks.length - 3} )
                  </button>
                )}
              </div>
            </div>
          </>
        )}
      </div>

      {/* Bottom Indicator */}
      <div style={{
        height: '24px',
        backgroundColor: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <div style={{
          width: '128px',
          height: '4px',
          backgroundColor: 'black',
          borderRadius: '20px',
          opacity: 0.3
        }}></div>
      </div>

      {/* Create Task Modal (/) */}
      <CreateMainlineTaskModal
        visible={mainlineModalVisible}
        onClose={() => setMainlineModalVisible(false)}
        onSubmit={handleCreateTask}
      />

      {/* All Sideline Tasks Drawer */}
      {showAllSidelineTasks && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            zIndex: 1000,
            animation: 'fadeIn 0.3s ease-out'
          }}
          onClick={() => setShowAllSidelineTasks(false)}
        >
          <div
            style={{
              position: 'absolute',
              bottom: 0,
              left: 0,
              right: 0,
              backgroundColor: 'white',
              borderTopLeftRadius: '20px',
              borderTopRightRadius: '20px',
              maxHeight: '80vh',
              display: 'flex',
              flexDirection: 'column',
              overflow: 'hidden',
              animation: 'slideUp 0.3s ease-out'
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Drawer Handle */}
            <div style={{
              padding: '12px 0',
              display: 'flex',
              justifyContent: 'center',
              borderBottom: '1px solid #f0f0f0'
            }}>
              <div style={{
                width: '40px',
                height: '4px',
                backgroundColor: '#ddd',
                borderRadius: '2px'
              }}></div>
            </div>

            {/* Drawer Header */}
            <div style={{
              padding: '16px 24px',
              borderBottom: '1px solid #f0f0f0',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between'
            }}>
              <h2 style={{
                margin: 0,
                fontSize: '18px',
                fontWeight: 'normal'
              }}> ({sidelineTasks.length})</h2>
              <button
                onClick={() => setShowAllSidelineTasks(false)}
                style={{
                  width: '32px',
                  height: '32px',
                  borderRadius: '8px',
                  border: 'none',
                  backgroundColor: 'transparent',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  cursor: 'pointer',
                  transition: 'background-color 0.2s'
                }}
                onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f5f5f5'}
                onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
              >
                <X size={20} />
              </button>
            </div>

            {/* Drawer Content */}
            <div style={{
              flex: 1,
              overflowY: 'auto',
              padding: '16px 24px',
              WebkitOverflowScrolling: 'touch',
              touchAction: 'pan-y'
            }}>
              <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
                {sidelineTasks.map(task => (
                  <SidelineTaskCard 
                    key={task.id} 
                    task={task}
                    onClick={() => setSelectedTaskId(task.id)}
                  />
                ))}
              </div>
            </div>
          </div>

          <style>{`
            @keyframes fadeIn {
              from {
                opacity: 0;
              }
              to {
                opacity: 1;
              }
            }
            
            @keyframes slideUp {
              from {
                transform: translateY(100%);
              }
              to {
                transform: translateY(0);
              }
            }
          `}</style>
        </div>
      )}

      {/* Goal Detail Modal */}
      <GoalDetailModal
        visible={!!selectedTaskId}
        goalId={selectedTaskId || ''}
        onClose={() => setSelectedTaskId(null)}
        onDataChange={refreshTasks}
      />

      {/* Archive List */}
      {showArchive && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'white',
          zIndex: 1000
        }}>
          <ArchiveList 
            onBack={() => setShowArchive(false)}
            onTaskClick={(taskId) => {
              setSelectedTaskId(taskId);
            }}
          />
        </div>
      )}

      {/* Settings Modal */}
      <Settings 
        visible={showSettings}
        onClose={() => setShowSettings(false)}
      />
    </div>
  );
}

export default function DemoPage() {
  return (
    <ThemeProvider>
      <TaskProvider>
        <DemoPageContent />
      </TaskProvider>
    </ThemeProvider>
  );
}
