import { useState } from 'react';
import GoalDetailModal from './index';

export default function GoalDetailExample() {
  const [detailVisible, setDetailVisible] = useState(false);
  const [selectedGoalId, setSelectedGoalId] = useState('');
  
  // 
  const mockGoals = [
    {
      id: 'goal-1',
      title: '30',
      icon: '',
      encouragement: '',
      type: 'mainline' as const,
      totalDays: 365,
      cycleDays: 10,
      totalCycles: 36,
      minCheckInsPerCycle: 3,
      startDate: '2025-01-01',
      checkIns: [
        { id: '1', date: '2025-01-02', timestamp: 1704153600000 },
        { id: '2', date: '2025-01-05', timestamp: 1704412800000 },
        { id: '3', date: '2025-01-08', timestamp: 1704672000000 }
      ]
    },
    {
      id: 'goal-2',
      title: '3',
      icon: '',
      encouragement: '',
      type: 'sidelineA' as const,
      priority: 'high' as const,
      totalDays: 90,
      cycleDays: 7,
      totalCycles: 12,
      minCheckInsPerCycle: 3,
      startDate: '2025-01-01',
      checkIns: [
        { id: '1', date: '2025-01-01', timestamp: 1704067200000 },
        { id: '2', date: '2025-01-03', timestamp: 1704240000000 }
      ]
    }
  ];
  
  //  localStorage
  const initMockData = () => {
    localStorage.setItem('dc_tasks', JSON.stringify(mockGoals));
    alert('');
  };
  
  const handleGoalClick = (goalId: string) => {
    setSelectedGoalId(goalId);
    setDetailVisible(true);
  };
  
  return (
    <div style={{ padding: '20px', maxWidth: '600px', margin: '0 auto' }}>
      <h1 style={{ fontSize: '24px', fontWeight: '600', marginBottom: '20px' }}>
        
      </h1>
      
      {/*  */}
      <button
        onClick={initMockData}
        style={{
          padding: '12px 24px',
          backgroundColor: '#4a9eff',
          color: 'white',
          border: 'none',
          borderRadius: '8px',
          fontSize: '14px',
          cursor: 'pointer',
          marginBottom: '20px'
        }}
      >
        
      </button>
      
      {/*  */}
      <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
        {mockGoals.map(goal => (
          <div
            key={goal.id}
            onClick={() => handleGoalClick(goal.id)}
            style={{
              padding: '16px',
              backgroundColor: 'white',
              border: '1px solid #e5e5e5',
              borderRadius: '12px',
              cursor: 'pointer',
              transition: 'all 0.2s'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = '#f8f8f8';
              e.currentTarget.style.transform = 'translateY(-2px)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = 'white';
              e.currentTarget.style.transform = 'translateY(0)';
            }}
          >
            <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
              <span style={{ fontSize: '32px' }}>{goal.icon}</span>
              <div style={{ flex: 1 }}>
                <div style={{ 
                  fontSize: '16px', 
                  fontWeight: '600',
                  marginBottom: '4px'
                }}>
                  {goal.title}
                </div>
                <div style={{ fontSize: '12px', color: '#999' }}>
                  {goal.type === 'mainline' ? ' ' : 
                   goal.type === 'sidelineA' ? ' A' : ' B'}
                  {'  '}
                  {goal.checkIns?.length || 0} 
                </div>
              </div>
              <span style={{ fontSize: '20px', color: '#999' }}></span>
            </div>
          </div>
        ))}
      </div>
      
      {/*  */}
      <GoalDetailModal
        visible={detailVisible}
        goalId={selectedGoalId}
        onClose={() => setDetailVisible(false)}
      />
      
      {/*  */}
      <div style={{
        marginTop: '40px',
        padding: '16px',
        backgroundColor: '#f8f8f8',
        borderRadius: '12px',
        fontSize: '14px',
        lineHeight: '1.6'
      }}>
        <h3 style={{ fontSize: '16px', fontWeight: '600', marginBottom: '12px' }}>
           
        </h3>
        <ol style={{ paddingLeft: '20px', margin: 0 }}>
          <li>"" localStorage</li>
          <li></li>
          <li></li>
          <li>Tab""""</li>
          <li></li>
          <li></li>
        </ol>
      </div>
    </div>
  );
}

