import { useState, useEffect, useMemo } from 'react';
import { Popup, Toast } from 'antd-mobile';
import { FileText, Check, Archive, Clock, Hash } from 'lucide-react';
import { useTheme } from '../settings/theme';
import dayjs from 'dayjs';
import GoalHeader from './GoalHeader';
import NumericCyclePanel from './NumericCyclePanel';
import ChecklistCyclePanel from './ChecklistCyclePanel';
import CheckInCyclePanel from './CheckInCyclePanel';
import HistoryRecordPanel from './HistoryRecordPanel';
import HistoryCyclePanel from './HistoryCyclePanel';
import CalendarViewPanel from './CalendarViewPanel';
import CheckInRecordPanel from './CheckInRecordPanel';
import RecordDataModal from './RecordDataModal';
import CheckInModal from './CheckInModal';
import CheckInHistoryPanel from './CheckInHistoryPanel';
import { useGoalDetail, getCurrentCycle, getSimulatedToday } from './hooks';
import type { GoalDetailModalProps } from './types';
import type { MainlineTaskType, CheckInUnit } from '../types';

// Tab
const getTabsConfig = (mainlineType: MainlineTaskType, isPlanEnded: boolean = false) => {
  // Tab""
  const firstTabLabel = isPlanEnded ? '' : '';
  
  switch (mainlineType) {
    case 'NUMERIC':
      return [
        { key: 'targets', label: firstTabLabel },
        { key: 'records', label: '' },
        { key: 'history', label: '' },
      ];
    case 'CHECKLIST':
      return [
        { key: 'current', label: firstTabLabel },
        { key: 'all', label: '' }
      ];
    case 'CHECK_IN':
    default:
      return [
        { key: 'cycle', label: firstTabLabel },
        { key: 'calendar', label: '' },
        { key: 'history', label: '' },
      ];
  }
};

// Tab
const getDefaultTab = (mainlineType: MainlineTaskType) => {
  switch (mainlineType) {
    case 'CHECKLIST': return 'current';
    default: return 'cycle';
  }
};

export default function GoalDetailModal({ 
  visible, 
  goalId, 
  onClose,
  onDataChange
}: GoalDetailModalProps) {
  const { 
    goalDetail, 
    loading, 
    checkInLoading, 
    checkIn,
    getTodayCheckInStatus,
    recordNumericData, 
    updateChecklistItem,
    debugNextCycle,
    debugNextDay,
    endPlanEarly,
    archiveTask
  } = useGoalDetail(goalId, onDataChange);
  const [activeTab, setActiveTab] = useState<string>('');
  const [showRecordModal, setShowRecordModal] = useState(false);
  const [showCheckInModal, setShowCheckInModal] = useState(false);
  
  // tab
  useEffect(() => {
    if (visible && goalDetail) {
      // tab
      let defaultTab = 'cycle';
      if (goalDetail.numericConfig) {
        defaultTab = 'targets';
      } else if (goalDetail.checklistConfig) {
        defaultTab = 'current';
      }
      setActiveTab(defaultTab);
    }
  }, [visible, goalDetail]);
  
  // 
  const handleCheckInClick = () => {
    const config = goalDetail?.checkInConfig;
    const unit = config?.unit || 'TIMES';
    
    if (unit === 'TIMES') {
      // 
      handleCheckInSubmit(1);
    } else {
      // 
      setShowCheckInModal(true);
    }
  };
  
  // 
  const handleCheckInSubmit = async (value?: number, note?: string) => {
    const success = await checkIn(value, note);
    if (success) {
      Toast.show({
        icon: 'success',
        content: '',
      });
      setShowCheckInModal(false);
    } else {
      const config = goalDetail?.checkInConfig;
      const todayStatus = getTodayCheckInStatus();
      if (todayStatus.isCompleted) {
        Toast.show({
          icon: 'fail',
          content: '',
        });
      } else {
        Toast.show({
          icon: 'fail',
          content: '',
        });
      }
    }
  };
  
  // 
  const handleRecordData = () => {
    setShowRecordModal(true);
  };
  
  // 
  const handleRecordSubmit = async (value: number, note?: string) => {
    const success = await recordNumericData(value, note);
    if (success) {
      Toast.show({
        icon: 'success',
        content: '',
      });
      setShowRecordModal(false);
    } else {
      Toast.show({
        icon: 'fail',
        content: '',
      });
    }
  };
  
  // 
  const handleUpdateProgress = async (itemId: string) => {
    // 
    const success = await updateChecklistItem(itemId, { status: 'COMPLETED' });
    if (success) {
      Toast.show({
        icon: 'success',
        content: '',
      });
    } else {
      Toast.show({
        icon: 'fail',
        content: '',
      });
    }
  };
  
  //  - 
  const isPlanEnded = useMemo(() => {
    if (!goalDetail) return false;
    const { cycleDays, totalCycles, startDate, cycleSnapshots, status } = goalDetail;
    const start = dayjs(startDate);
    // ""
    const simulatedToday = getSimulatedToday(goalDetail);
    const today = dayjs(simulatedToday);
    const planEndDate = start.add(totalCycles * cycleDays - 1, 'day');
    //   status  cycleSnapshots  
    const isPlanEndedByTime = today.isAfter(planEndDate);
    const isPlanEndedByStatus = status === 'completed' || status === 'archived';
    const isPlanEndedBySnapshots = (cycleSnapshots?.length || 0) >= totalCycles;
    return isPlanEndedByTime || isPlanEndedByStatus || isPlanEndedBySnapshots;
  }, [goalDetail]);
  
  const { themeColors } = useTheme();
  
  if (loading || !goalDetail) {
    return null;
  }
  
  const currentCycle = getCurrentCycle(goalDetail);

  console.log(currentCycle, goalDetail, 'currentCycle')
  
  // 
  const getEffectiveMainlineType = (): MainlineTaskType => {
    // numericConfig
    if (goalDetail.numericConfig) {
      return 'NUMERIC';
    }
    // checklistConfig
    if (goalDetail.checklistConfig) {
      return 'CHECKLIST';
    }
    // mainlineType
    return 'CHECK_IN';
  };
  
  const mainlineType = getEffectiveMainlineType();
  const tabs = getTabsConfig(mainlineType, isPlanEnded);
  
  // 
  const totalCheckIns = goalDetail.checkIns?.length || 0;
  
  // 
  const cycleRecords = goalDetail.checkIns?.filter(checkIn => {
    const checkInDate = new Date(checkIn.date);
    const cycleStart = new Date(currentCycle.startDate);
    const cycleEnd = new Date(currentCycle.endDate);
    return checkInDate >= cycleStart && checkInDate <= cycleEnd;
  }) || [];
  
  // Tab
  const renderContent = () => {
    switch (mainlineType) {
      case 'NUMERIC':
        if (activeTab === 'targets') {
          return <NumericCyclePanel goal={goalDetail} cycle={currentCycle} onRecordData={handleRecordData} />;
        }
        if (activeTab === 'records') {
          return <HistoryRecordPanel goal={goalDetail} />;
        }
        if (activeTab === 'history') {
          return <HistoryCyclePanel goal={goalDetail} />;
        }
        return null;
        
      case 'CHECKLIST':
        if (activeTab === 'current' || activeTab === 'cycle') {
          return <ChecklistCyclePanel goal={goalDetail} cycle={currentCycle} onUpdateProgress={handleUpdateProgress} />;
        }
        return <div style={{ padding: 20, textAlign: 'center', color: '#999' }}>...</div>;
        
      case 'CHECK_IN':
      default:
        if (activeTab === 'cycle') {
          return <CheckInCyclePanel goal={goalDetail} cycle={currentCycle} />;
        }
        if (activeTab === 'calendar') {
          return <CalendarViewPanel goal={goalDetail} />;
        }
        if (activeTab === 'history') {
          return <CheckInHistoryPanel goal={goalDetail} />;
        }
        if (activeTab === 'records') {
          return (
            <CheckInRecordPanel
              records={cycleRecords}
              cycleStartDate={currentCycle.startDate}
              cycleEndDate={currentCycle.endDate}
            />
          );
        }
        return null;
    }
  };
  
  // 
  const todayCheckInStatus = getTodayCheckInStatus();
  
  // 
  const getButtonText = () => {
    // 
    if (isPlanEnded) {
      return <><Archive size={16} style={{ marginRight: 6 }} /> </>;
    }
    switch (mainlineType) {
      case 'NUMERIC': return <><FileText size={16} style={{ marginRight: 6 }} /> </>;
      case 'CHECKLIST': return <><FileText size={16} style={{ marginRight: 6 }} /> </>;
      case 'CHECK_IN':
      default: {
        const config = goalDetail?.checkInConfig;
        const unit = config?.unit || 'TIMES';
        
        // 
        if (todayCheckInStatus.isCompleted) {
          return <><Check size={16} style={{ marginRight: 6 }} /> </>;
        }
        
        if (unit === 'DURATION') {
          return <><Clock size={16} style={{ marginRight: 6 }} /> </>;
        } else if (unit === 'QUANTITY') {
          return <><Hash size={16} style={{ marginRight: 6 }} /> </>;
        }
        return <><Check size={16} style={{ marginRight: 6 }} /> </>;
      }
    }
  };
  
  // 
  const isCheckInButtonDisabled = () => {
    // 
    if (isPlanEnded) return false;
    if (mainlineType !== 'CHECK_IN') return false;
    return todayCheckInStatus.isCompleted;
  };
  
  // 
  const handleArchive = async () => {
    const success = await archiveTask();
    if (success) {
      Toast.show({
        icon: 'success',
        content: '',
      });
      onClose();
    } else {
      Toast.show({
        icon: 'fail',
        content: '',
      });
    }
  };
  
  // 
  const getButtonHandler = () => {
    // 
    if (isPlanEnded) {
      return handleArchive;
    }
    switch (mainlineType) {
      case 'NUMERIC': return handleRecordData;
      case 'CHECKLIST': return () => handleUpdateProgress('');
      case 'CHECK_IN':
      default: return handleCheckInClick;
    }
  };
  
  return (
    <Popup
      visible={visible}
      onMaskClick={onClose}
      position='bottom'
      destroyOnClose={false}
      forceRender={false}
      bodyStyle={{
        borderTopLeftRadius: '16px',
        borderTopRightRadius: '16px',
        height: '90vh',
        display: 'flex',
        flexDirection: 'column',
        overflow: 'hidden',
        background: '#f5f5f5'
      }}
    >
      {/*  +  */}
      <GoalHeader 
        goal={goalDetail} 
        onClose={onClose}
        currentCheckIns={currentCycle.checkInCount}
        requiredCheckIns={currentCycle.requiredCheckIns}
        totalCheckIns={totalCheckIns}
        totalCycles={goalDetail.totalCycles}
        currentCycle={currentCycle.cycleNumber}
        remainingDays={currentCycle.remainingDays}
        isPlanEnded={isPlanEnded}
        onDebugNextDay={async () => {
          const result = await debugNextDay();
          if (result && result.success) {
            Toast.show({
              icon: 'success',
              content: result.enteredNextCycle ? '' : '',
            });
          }
        }}
        onDebugNextCycle={async () => {
          const success = await debugNextCycle();
          if (success) {
            Toast.show({
              icon: 'success',
              content: '',
            });
          }
        }}
        onEndPlanEarly={async () => {
          const success = await endPlanEarly();
          if (success) {
            Toast.show({
              icon: 'success',
              content: '',
            });
          }
        }}
        onConvertToSideline={() => {
          Toast.show({
            content: '...',
          });
        }}
      />
      
      {/* Tab -  */}
      <div style={{ 
        display: 'flex',
        gap: '12px',
        padding: '8px 20px 8px',
        flexShrink: 0,
        overflowX: 'auto',
        borderBottom: '1px dashed #ddd',
        borderTop: '1px dashed #ddd',
        background: '#fff',
        WebkitOverflowScrolling: 'touch'
      }}>
        {tabs.map(tab => (
          <div
            key={tab.key}
            onClick={() => setActiveTab(tab.key)}
            style={{
              height: '32px',
              padding: '0 16px',
              borderRadius: '8px',
              fontSize: '14px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              cursor: 'pointer',
              flexShrink: 0,
              transition: 'all 0.3s',
              background: activeTab === tab.key ? '#F5F5F5' : 'transparent',
              color: activeTab === tab.key ? '#141414' : '#525252'
            }}
          >
            {tab.label}
          </div>
        ))}
      </div>
      
      {/*  -  */}
      <div style={{ 
        flex: 1, 
        overflowY: 'auto', 
        background: '#fff',
        WebkitOverflowScrolling: 'touch'
      }}>
        {renderContent()}
      </div>
      
      {/*  - Tab */}
      {(activeTab === 'targets' || activeTab === 'current' || activeTab === 'cycle') && goalDetail.status !== 'archived' && (
        <div style={{
          padding: '16px 20px',
          paddingBottom: 'calc(16px + env(safe-area-inset-bottom))',
          background: '#fff',
          flexShrink: 0
        }}>
          <button
            onClick={getButtonHandler()}
            disabled={checkInLoading || isCheckInButtonDisabled()}
            style={{
              width: '100%',
              height: '52px',
              background: (checkInLoading || isCheckInButtonDisabled()) ? '#ccc' : themeColors.primary,
              color: '#fff',
              border: 'none',
              borderRadius: '26px',
              fontSize: '16px',
              fontWeight: '600',
              cursor: (checkInLoading || isCheckInButtonDisabled()) ? 'not-allowed' : 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              gap: '8px',
              transition: 'all 0.2s'
            }}
          >
            {checkInLoading ? '...' : getButtonText()}
          </button>
        </div>
      )}
      
      {/*  */}
      {mainlineType === 'NUMERIC' && goalDetail.numericConfig && (
        <RecordDataModal
          visible={showRecordModal}
          onClose={() => setShowRecordModal(false)}
          onSubmit={handleRecordSubmit}
          currentValue={goalDetail.numericConfig.currentValue}
          unit={goalDetail.numericConfig.unit}
          direction={goalDetail.numericConfig.direction}
          loading={checkInLoading}
        />
      )}
      
      {/*  */}
      {mainlineType === 'CHECK_IN' && goalDetail.checkInConfig && (
        <CheckInModal
          visible={showCheckInModal}
          onClose={() => setShowCheckInModal(false)}
          onSubmit={handleCheckInSubmit}
          unit={goalDetail.checkInConfig.unit || 'TIMES'}
          loading={checkInLoading}
          quickDurations={goalDetail.checkInConfig.quickDurations || [5, 10, 15]}
          dailyTargetMinutes={goalDetail.checkInConfig.dailyTargetMinutes || 15}
          valueUnit={goalDetail.checkInConfig.valueUnit || ''}
          dailyTargetValue={goalDetail.checkInConfig.dailyTargetValue || 0}
          todayValue={getTodayCheckInStatus().todayValue}
        />
      )}
    </Popup>
  );
}



